[data-theme="light"] {
  --bgColor: white;
  --btnBgColor: cadetblue;
  --textColor: black;
  --btnTextColor: white;
  --border: 1px solid #000;
}
[data-theme="dark"] {
  --bgColor: black;
  --btnBgColor: aliceblue;
  --textColor: white;
  --btnTextColor: black;
  --border: 1px solid #fff;
}
[data-theme="orange"] {
  --bgColor: orange;
  --btnBgColor: cornsilk;
  --textColor: blue;
  --btnTextColor: blue;
  --border: 1px solid blue;
}
.box {
  height: 100%;
  width: 100%;
}
.moduleBox {
  background: var(--bgColor);
  color: var(--textColor);
  height: 100%;
  width: 100%;
  padding: 16px;
  transition: all 0.5s;
}
.moduleBox > div:nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn {
  width: 100px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 16px;
  margin-right: 8px;
  cursor: pointer;
  border: var(--border);
}
.actBtn {
  background: var(--btnBgColor);
  color: var(--btnTextColor);
}
.noChange {
  font-size: 24px;
  color: red;
}
.txt {
  font-size: 24px;
}
